.container.main{
  margin-top: 75px;
}
.col{
  color: white;
}

.row.category{
  border: 4px solid #46244C;
  background-color: #8B9A46;
  border-radius: 20px;
  padding-top: 20px;
  transition: .3s;
  box-shadow: -10px -10px 0px 0px #184c80

  ;
}

.row.category.disabled{
  filter: grayscale(90%);
}

.row.category:nth-of-type(1){
  animation: showCategory .5s backwards;
}

.row.category:nth-of-type(2){
  animation: showCategory .5s .5s backwards;
}

.row.category:nth-of-type(3){
  animation: showCategory .5s 1s backwards;
}

.row.category:nth-of-type(4){
  animation: showCategory .5s 1.5s backwards;
}


h1{
  color: white;
  font-family: 'roboto';
}

.title{
  position: relative;
  top: 50px;
}
p{
  font-size: 24px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.description{
  margin-top: 75px;
  padding: 0 30px;
  text-align: justify;
  color: white;
}

.rights{
  margin-top: 50px;
  font-size: 12px;
  color: white;
}
.start{
  transition: .4s;
}
.start:hover{
  transform: scale(1.2);
}

p.popover{
  padding: 2px;
  font-size: 14px;
  font-weight: bold;
  border: none;
  background-color: #BFEAF5;
}

.col{
  padding: 0;
}
img{
  padding: 0;
}

@keyframes showCategory {
  0%{
    transform: scale(0);
  }
  100%{
    transform: scale(1);
  }
}
